.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.scroll-image {
  top: 539.111px;
  position: absolute;
  top: 0;
  width: 100%;
  height: 600px;
  filter: brightness(40%);
  z-index: -1;
}

.header-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.left {
  margin-top: 150px;
  margin-left: 100px;
}

.right {
  line-height: 15px;
  margin-top: 150px;
  margin-right: 100px;
}

.left .profile-pic {
  margin-bottom: 0;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  object-fit: cover;
}

.header h1 {
  margin: 0;
  color: #ffffff;
  font-size: 32px;
}

.right a {
  margin-right: 10px;
  color: #1E90FF; 
  font-size: 22px;
  text-decoration: underline;
  margin: 5px 0;
}

.right a:hover {
  color: #ff1ef8;
}

.right p {
  margin: 0;
  color: #ffffff;
  font-size: 18px;
  text-align: center;
}

.light-blue-icon {
  color: #135fca;
}

#me {
  color: #ffffff;
  font-size: large;
  position: relative;
  margin-top: 30px;
  margin-bottom: 50px;
  margin-left: 250px;
}
